.atlas-sidebar {
  display: flex;
  flex-direction: column;
  flex: 1 0;
  background: #FBFCFC;
  margin-top: 10px;
  border-radius: 5px;
  border: 1px solid rgba(131, 145, 146, 0.25); }
  .atlas-sidebar .header {
    display: flex;
    justify-content: center;
    padding: 10px 0;
    border-bottom: 1px solid rgba(131, 145, 146, 0.25); }
  .atlas-sidebar .component-list {
    display: flex;
    flex: 1;
    flex-direction: column;
    height: 100%; }
    .atlas-sidebar .component-list .component-row {
      display: flex;
      align-items: center;
      height: 20px;
      font-size: 15px;
      cursor: pointer;
      padding: 10px;
      padding-left: 20px;
      transition: all 250ms ease-in-out; }
    .atlas-sidebar .component-list .component-row:hover {
      color: #5499C7; }
    .atlas-sidebar .component-list .selected {
      background-color: #FCF3CF;
      height: 30px; }

.component-holder {
  display: flex;
  flex-direction: column;
  flex: 5 0;
  margin: 10px 0 0 10px;
  padding: 10px;
  background: #FBFCFC;
  border-radius: 10px;
  border: 1px solid rgba(131, 145, 146, 0.25); }
  .component-holder .main-title {
    display: flex;
    justify-content: flex-end;
    font-size: 35px;
    animation: fade-in 1.5s 1;
    padding: 10px; }
  .component-holder .demo-holder {
    padding: 10px; }

@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.doc-root {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
  height: 96vh;
  margin: 2vh 0 2vh 0; }
  .doc-root .atlas-header {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #5499C7;
    color: white;
    border-radius: 5px;
    height: 75px; }
  .doc-root .main-root {
    display: flex;
    flex: 1; }

body {
  font-family: 'Source Sans Pro', sans-serif;
  color: #424949;
  max-width: 1000px;
  margin: 0 auto;
  background-color: rgba(234, 236, 238, 0.5); }



